.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* General styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #333;
}

/* Flashcard container */
.flashcard-container {
  width: 90%;
  max-width: 400px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s;
}

/* Card content */
.flashcard-content {
  padding: 20px;
  text-align: center;
}

.flashcard-content h2 {
  font-size: 1.2em;
  margin-bottom: 15px;
  color: #0077cc;
}

.flashcard-content p {
  font-size: 1em;
  line-height: 1.6;
  margin: 0;
}

/* Button group */
.flashcard-buttons {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  background-color: #f9f9f9;
  border-top: 1px solid #ddd;
}

.flashcard-buttons button {
  background: #0077cc;
  border: none;
  color: #fff;
  font-size: 1em;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.flashcard-buttons button:hover {
  background-color: #005fa3;
}

/* Navigation buttons */
.nav-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.nav-buttons button {
  background: none;
  border: none;
  color: #0077cc;
  font-size: 1.5em;
  cursor: pointer;
  transition: color 0.3s;
}

.nav-buttons button:hover {
  color: #005fa3;
}

/* Flip card animation */
.flashcard {
  cursor: pointer;
  transform-style: preserve-3d;
  perspective: 1000px;
  position: relative;
  height: 200px;
  width: 100%;
}

.flashcard-content.front {
  position: absolute;
  backface-visibility: hidden;
}

.flashcard-content.back {
  position: absolute;
  backface-visibility: hidden;
  transform: rotateY(180deg);
}

.flashcard.is-flipped .front {
  transform: rotateY(180deg);
}

.flashcard.is-flipped .back {
  transform: rotateY(0deg);
}

/* Responsive design */
@media (max-width: 768px) {
  .flashcard-container {
    width: 100%;
  }

  .flashcard-buttons button {
    font-size: 0.9em;
  }

  .nav-buttons button {
    font-size: 1.2em;
  }
}


